<template>
  <Transition name="fade">
    <div class="modal-overlay">
      <div class="modal-content-wide" ref="modalRef">
        <!-- üîπ 1. Title ÏòÅÏó≠ -->
        <div class="modal-header" @mousedown="startDrag">
          <span>ÏÇ¨ÏóÖÏû• Í≤ÄÏÉâ</span>
          <button class="icon-button" @click="$emit('close')">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="currentColor"
              class="w-6 h-6"
            >
              <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
        <!-- üîπ 2. Ï°∞Ìöå Form ÏòÅÏó≠ -->
        <div class="viewSearch">
          <div class="form-left">
            <label>ÏÇ¨ÏóÖÏû•Î≤àÌò∏</label>
            <input v-model="siteNo" />
            <label>ÏÇ¨ÏóÖÏû•Î™Ö</label>
            <input v-model="siteNm" />
          </div>
          <div class="btn-group">
            <button class="btn btn-primary" @click="fnSearch">Ï°∞Ìöå</button>
          </div>
        </div>

        <!-- üîπ 3. Í∑∏Î¶¨Îìú ÏòÅÏó≠ -->
        <div class="viewBody">
          <div class="table-wrapper">
            <table class="data-grid">
              <thead>
                <tr>
                  <th style="display: none">ÏÇ¨ÏóÖÏû•ÏΩîÎìú</th>
                  <th>ÏÇ¨ÏóÖÏû•Î≤àÌò∏</th>
                  <th>ÏÇ¨ÏóÖÏû•Î™Ö</th>
                  <th>Í¥ÄÎ¶¨ÏûêÎ™Ö</th>
                  <th>ÏÇ¨ÏóÖÏû• Ï†ÑÌôîÎ≤àÌò∏</th>
                  <th>Ï£ºÏÜå</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="site in siteList"
                  :key="site.SITE_CD"
                  @dblclick="fnSelectRow(site.SITE_CD, site.SITE_NO, site.SITE_NM)"
                >
                  <td style="display: none">{{ site.SITE_CD }}</td>
                  <td>{{ site.SITE_NO }}</td>
                  <td>{{ site.SITE_NM }}</td>
                  <td>{{ site.SITE_ADMIN_ID }}</td>
                  <td>
                    {{
                      proxy.$util.isNotEmpty(site.TEL_NO)
                        ? proxy.$util.formatPhoneNumber(site.TEL_NO)
                        : site.TEL_NO
                    }}
                  </td>
                  <td>{{ site.ADDR_1 || site.ADDR_2 }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script setup>
import { defineProps, defineEmits, ref, getCurrentInstance, onMounted } from 'vue'
import axios from '@/api/axios'

const cmpnyCd = ref('')
const siteList = ref([])
const emit = defineEmits(['select', 'close'])

const { proxy } = getCurrentInstance()

const props = defineProps({
  visible: Boolean,
  cmpnyCd_p: String,
  siteNo_p: String,
  siteNm_p: String,
  onSelect: Function,
})

onMounted(async () => {
  cmpnyCd.value = props.cmpnyCd_p

  if (props.siteNo_p) siteNo.value = props.siteNo_p
  if (props.siteNm_p) siteNm.value = props.siteNm_p
  fnSearch() // visibleÏù¥ trueÏùº ÎïåÎßå Ìò∏Ï∂ú
})

const siteNo = ref('')
const siteNm = ref('')

const fnSearch = async () => {
  siteList.value = []
  try {
    if (!proxy.$util.isEmpty(cmpnyCd.value)) {
      const response = await axios.post('/comApi/baseinfo/getSiteInfoList', {
        cmpnyCd: cmpnyCd.value,
        siteNo: siteNo.value,
        siteNm: siteNm.value,
      })

      if (response.status === 200) {
        siteList.value = response.data
      }
    }
  } catch (err) {
    alert(err.response.data.message)
  }
}

function fnSelectRow(siteCd, siteNo, siteNm) {
  // emit("select", siteCd, siteNo, siteNm); // SITE_CD Î∂ÄÎ™®Ïóê Ï†ÑÎã¨
  props.onSelect(siteCd, siteNo, siteNm)
  emit('close') // ÌåùÏóÖ Îã´Í∏∞
}
</script>
